import { Button, ButtonProps } from '@chakra-ui/react'
import * as React from 'react'

export interface IProps extends ButtonProps {
	color: string
	onClick?: () => void
}
export default (props: IProps) => {
	const { color, backgroundColor, onClick, ...rest } = props
	return (
		<Button
			style={{ color }}
			onClick={() => onClick()}
			borderRadius={12}
			size='md'
			minH='35px'
			minW='200px'
			backgroundColor='#fcfa3b'
		>
			Color Button
		</Button>
	)
}
